
import Rater from 'vux/src/components/rater/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import VHeader from '@/components/v-header/v-header'
import { isIphoneX } from '@/assets/js/brower'
import Scroll from '@/components/scroll/scroll'
import Comment from '@/components/comment/comment'
import axios from '@/api/axiosApi'

import { mapGetters } from 'vuex'

import { localUser } from '@/assets/js/local'
import imageTop from '@/assets/img/default_avatar.png'

export default {
data () {
return {
paddingTop: 0,
commentList: [],
value: '',
data: [],
currentCommentNickname: '',
targetType: '0',
itemm: '',
headerimage: imageTop
}
},
components: {
VHeader,
Scroll,
Comment,
Rater,
XInput
},
computed: {
...mapGetters([
'currentComment'
])
},
created () {
if (isIphoneX()) {
this.paddingTop = 80
} else {
this.paddingTop = 65
}
},
mounted () {

this.productData()

},
methods: {
productData () {

axios.get(this, 'v1/reply/' + this.currentComment.id, null, (data) => {
this.data = data
})
this.currentCommentNickname = '@' + this.currentComment.nickname
},
show (ev) {

const user = localUser.get()

if (ev.keyCode === 13) {
if (this.value === '' && this.targetType === '0') {
return
} else {




axios.post(this, '/v1/reply/', {
targetId: this.currentComment.productId,
userId: user.id,
targetType: this.targetType,
comment: this.value,



targetUserId: this.currentComment.userId,
targetCommentId: this.currentComment.id
}, (data) => {
this.value = ''

this.productData()
})

}
}
if (ev.keyCode === 8) {
this.value = ''
this.currentCommentNickname = '@' + this.currentComment.nickname
}
},
commentReply (item) {
let itemm = item
this.currentCommentNickname = '@' + itemm.userName
this.value = '@' + itemm.userName + ':'
},
commentReplyFloorHost () {
this.currentCommentNickname = '@' + this.currentComment.nickname
this.targetType = '0'
this.value = ''
}







}
}



